import Header from '../components/Header';
import Footer from '../components/Footer';
import styles from '../styles/Observa.module.css'
import React, { useRef, useState } from 'react'; // React e hooks
import {useNavigate } from 'react-router-dom'; // Navegao
import axios from 'axios'; // Biblioteca para requisies HTTP


    

const Observa = () => {

    const handleCardClick = (id) => {
        navigate(`/FullPost/${id}`);
    };
    const navigate = useNavigate(); // Hook para navegao
    const [posts, setPosts] = useState([]); // Estado para armazenar a lista de posts
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null); // Estado para armazenar mensagens de erro
    const handleCategory = async(category)=>{
        try {
            const response = await axios.get(`http://localhost:8080/blog/category/${category}`);
            navigate('/Category',{state:{posts:response.data}});
        } catch (error)
        {
            console.error(`Erro ao buscar posts da categoria ${category}`, error);
            setError("Erro ao carregar posts da categoria. Tente novamente.");    
        }
    };
    const fetchPosts = async () => {
        try {
            const response = await axios.get('http://localhost:8080/blog/list');
            if (response.status === 200 && response.data.length > 0) {
                const sortedPosts = response.data.sort((a, b) => new Date(b.datacreation) - new Date(a.datacreation));
                setPosts(sortedPosts);
            }
        } catch (error) {
            console.error("Erro ao carregar os posts: ", error);
            setError("Erro ao carregar os posts. Tente novamente.");
        } finally {
            setLoading(false);
        }
    };
    const navRef = useRef();

    const showNavbar = () => {
        console.log(navRef.current); // Verifica se o `ref` est funcionando
        navRef.current.classList.toggle("responsive_nav");
    };
  
    return (
        <div>
            <Header />
            <main>
                <div className={styles.obscontainer}>

                <div 
                    className={`${styles.imageCenter1} observaimg-container`} 
                    onClick={() => handleCategory('observActiva')} 
                style={{ cursor: 'pointer' }}
>
                <img 
                className="observaimg" 
                src="/images/observatorio.webp" 
                alt="Logo" 
                 />
                </div>
                
                    <div className={styles.textSection}>

                        <p>O <strong>Observatrio de Conflitualidades em Polticas Sociais e Ambientais na Amrica Latina e Caribe </strong>(ObservActiva)  uma iniciativa que se dedica  captao, observao e anlise sistemtica de conflitos socioambientais envolvendo a sociedade civil e o Estado e outro setor, como o privado. Nosso foco principal est na investigao de litgios relacionados aos direitos sociais, entendidos de forma ampla, tendo como marco referencial a <strong>Declarao sobre o Direito ao Desenvolvimento (1986).</strong>
                        <br></br><br></br>
                        Desde a sua criao, a ObservActiva tem produzido, de forma contnua, relatrios de pesquisa que abordam as dinmicas sociais e ambientais nas diversas regies da Amrica Latina e do Caribe. At o momento, publicamos 6 relatrios de pesquisa, alm de documentos colaborativos com organizaes parceiras, como a <i>Red Latinoamericana por Justicia Econmica y Social</i> (Latindadd), e diversas notas informativas.  
                         <span onClick={() => handleCategory('observActiva')} style={{  textDecoration: 'underline', cursor: 'pointer' }}
                >
                    <strong>Acesse nossos materiais para mais detalhes</strong>
                </span>
                        <br></br> <br></br>
                        <br></br> <br></br>
                        No final desta pgina, voc encontrar nossas informaes de contato, alm de um infogrfico que apresenta, de forma resumida, os dados mapeados pela ObservActiva at o momento. O infogrfico inclui, entre outras informaes, os territrios de maior dificuldade identificados pela nossa equipe de pesquisa e os maiores agrupamentos de conflitos.</p> 
                        <br></br>
                        

                    </div>

                    <div className={styles.imagesSideBySide}>
                    <div onClick={() => handleCategory('observActiva')} 
                style={{ cursor: 'pointer' }}>
                    <img 
                    className={styles.observaimg} 
                    src="/images/relatorio.webp" 
                    alt="Logo Relatrio" 
                        />
                    </div>

                        <a href="https://ee-eu.kobotoolbox.org/x/wvGcywe2" target="_blank" rel="noopener noreferrer"> 
                            <img className={styles.observaimg2} src="/images/formulario.webp" alt="Logo" /></a>
                    </div>

                    <div className={styles.textSection}>
                        <h3>COMO PARTICIPAR</h3>
                        <br></br>

                        <p>Se voc tem interesse nas reas da nossa pesquisa,  possvel participar como pesquisador(a) ou curador(a) da ObservActiva, entre <strong><u><a href='/contato' target='_blank' rel="noopener noreferrer">em contato</a></u></strong> conosco para saber mais sobre as oportunidades de participao voluntria.</p>
                        <br></br><p>Alm disso, se sua organizao deseja se tornar um ncleo-parceiro da nossa rede, tambm ficaremos felizes em receber seu contato.</p>

                    </div>
                    <div className={styles.imageCenter2}>
                        <img className="observaimg" src="/images/dados.jpeg" alt="Logo" />
                        <p>*Os dados indicados neste infogrfico foram coletados em maro de 2024. </p>
                    </div>
                    
                </div>    
            </main>
            <Footer />
        </div>
    );
};

export default Observa;