// src/components/ProtectedRoute.jsx
import React from 'react';
import { Navigate } from 'react-router-dom';
import { useAuth } from './AuthContext';

const ProtectedRoute = ({ element, requiredAccess }) => {
    const { user } = useAuth(); // Obtenha as informaes do usurio do contexto

    // Verifique se o usurio est autenticado e se possui o nvel de acesso necessrio
    if (!user || (requiredAccess && !requiredAccess.includes(user.role))) {
        // Redirecione para a pgina de login se no estiver autenticado
        return <Navigate to="/error" />;
    }

    // Se o usurio estiver autenticado e tiver acesso, renderize o componente
    return element;
};

export default ProtectedRoute;
